import Swiper, { Navigation, Pagination } from 'swiper';
Swiper.use([Navigation]);

const swiperBefore = '.before-services__swiper';
const beforeList = document.querySelector('.before-services__list');
let beforeSwiper;
if (document.querySelector(swiperBefore)) {
  beforeSwiper = new Swiper(swiperBefore, {
      slidesPerView: 'auto',
      spaceBetween: 10,
      loop: true,
      breakpoints: {
          576: {
              slidesPerView: 2,
              spaceBetween: 20  
          },
          1200: {
            slidesPerView: 2,
            spaceBetween: 10
          }
      },
      on: {
        touchMove: function(e, evt) {
          if(evt.target.classList.contains('beer-range')) {
            beforeSwiper.disable();
          } else {
            beforeSwiper.enable();
          };
        }
      }
  });
}

